import { defineComponent, ref, computed, nextTick } from 'vue';
import { formconfig } from './config/form.config';
import { tableconfig } from './config/table.config';
import { modalconfig } from './config/modal.config';
import PageSearch from '@/components/page-search';
import PageContnet from '@/components/page-content';
import PageModal from '@/components/page-modal';
import { usePageModal } from '@/hooks/use-page-modal';
import { useStore } from 'vuex';
import { menusMapLeafKeys } from '@/utils/map-menus';
import { usePageSearch } from '@/hooks/use-page-search';
export default defineComponent({
    name: 'role',
    components: {
        PageSearch,
        PageContnet,
        PageModal
    },
    setup() {
        let [pageContentRef, handleResetEvent, handlesearchEvent] = usePageSearch();
        // modal的配置
        const elTreeRef = ref();
        const updatedataCb = (item) => {
            // console.log(item)
            const leafKeys = menusMapLeafKeys(item.menuList);
            // console.log(leafKeys)
            nextTick(() => {
                elTreeRef.value.setCheckedKeys(leafKeys, false);
            });
        };
        const [pageModalRef, defaultInfo, handleCreate, handleUpdate] = usePageModal(updatedataCb, undefined);
        const store = useStore();
        const menus = computed(() => store.state.entireMenus);
        const otherInfo = ref({});
        const handleCheckChange = (obj1, obj2) => {
            const checkedKeys = obj2.checkedKeys;
            const halfCheckedKeys = obj2.halfCheckedKeys;
            const menuList = [...halfCheckedKeys, ...checkedKeys];
            otherInfo.value = { menuList };
        };
        return {
            formconfig,
            tableconfig,
            pageModalRef,
            defaultInfo,
            handleCreate,
            handleUpdate,
            modalconfig,
            menus,
            handleCheckChange,
            otherInfo,
            elTreeRef,
            pageContentRef,
            handleResetEvent,
            handlesearchEvent
        };
    }
});
